"use client"

import { useState, useEffect } from "react"
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { Badge } from "@/components/ui/badge"
import { Separator } from "@/components/ui/separator"

interface TrackingData {
  id: number
  licensePlate: string
  vehicle: string
  driver: string
  status: "active" | "parked" | "maintenance" | "offline"
  location: string
  speed: number
  lastUpdate: string
  coordinates: { lat: number; lng: number }
  route: string
  fuelLevel: number
  mileage: number
}

const mockTrackingData: TrackingData[] = [
  {
    id: 1,
    licensePlate: "ABC-123",
    vehicle: "2022 Toyota Camry",
    driver: "John Smith",
    status: "active",
    location: "Downtown Business District",
    speed: 35,
    lastUpdate: "2 minutes ago",
    coordinates: { lat: 40.7128, lng: -74.0060 },
    route: "Route A - City Center",
    fuelLevel: 75,
    mileage: 25420,
  },
  {
    id: 2,
    licensePlate: "XYZ-789",
    vehicle: "2023 Honda CR-V",
    driver: "Sarah Johnson",
    status: "parked",
    location: "Airport Terminal 2",
    speed: 0,
    lastUpdate: "15 minutes ago",
    coordinates: { lat: 40.6892, lng: -74.1745 },
    route: "Route B - Airport",
    fuelLevel: 60,
    mileage: 15230,
  },
  {
    id: 3,
    licensePlate: "LUX-456",
    vehicle: "2021 BMW 3 Series",
    driver: "Mike Davis",
    status: "maintenance",
    location: "Service Center",
    speed: 0,
    lastUpdate: "1 hour ago",
    coordinates: { lat: 40.7589, lng: -73.9851 },
    route: "Off Route - Maintenance",
    fuelLevel: 30,
    mileage: 35680,
  },
  {
    id: 4,
    licensePlate: "VAN-321",
    vehicle: "2020 Ford Transit",
    driver: "Lisa Wilson",
    status: "active",
    location: "Industrial District",
    speed: 45,
    lastUpdate: "1 minute ago",
    coordinates: { lat: 40.6782, lng: -73.9442 },
    route: "Route C - Delivery",
    fuelLevel: 85,
    mileage: 45120,
  },
  {
    id: 5,
    licensePlate: "ECO-555",
    vehicle: "2023 Tesla Model 3",
    driver: "David Brown",
    status: "offline",
    location: "Unknown",
    speed: 0,
    lastUpdate: "3 hours ago",
    coordinates: { lat: 40.7505, lng: -73.9934 },
    route: "Route D - Uptown",
    fuelLevel: 45,
    mileage: 12890,
  },
]

export default function TrackingPage() {
  const [trackingData, setTrackingData] = useState<TrackingData[]>(mockTrackingData)
  const [selectedVehicle, setSelectedVehicle] = useState<TrackingData | null>(null)
  const [searchTerm, setSearchTerm] = useState("")
  const [statusFilter, setStatusFilter] = useState("all")
  const [trackingInput, setTrackingInput] = useState("")

  // Simulate real-time updates
  useEffect(() => {
    const interval = setInterval(() => {
      setTrackingData(prev => prev.map(vehicle => {
        if (vehicle.status === "active") {
          return {
            ...vehicle,
            speed: Math.max(0, vehicle.speed + (Math.random() - 0.5) * 10),
            lastUpdate: "Just now",
            fuelLevel: Math.max(0, vehicle.fuelLevel - Math.random() * 0.1),
          }
        }
        return vehicle
      }))
    }, 5000)

    return () => clearInterval(interval)
  }, [])

  const filteredVehicles = trackingData.filter((vehicle) => {
    const matchesSearch = 
      vehicle.licensePlate.toLowerCase().includes(searchTerm.toLowerCase()) ||
      vehicle.vehicle.toLowerCase().includes(searchTerm.toLowerCase()) ||
      vehicle.driver.toLowerCase().includes(searchTerm.toLowerCase())
    const matchesStatus = statusFilter === "all" || vehicle.status === statusFilter
    return matchesSearch && matchesStatus
  })

  const handleTrackVehicle = () => {
    if (!trackingInput.trim()) return
    
    const vehicle = trackingData.find(v => 
      v.licensePlate.toLowerCase() === trackingInput.toLowerCase() ||
      v.vehicle.toLowerCase().includes(trackingInput.toLowerCase())
    )
    
    if (vehicle) {
      setSelectedVehicle(vehicle)
      setTrackingInput("")
    } else {
      alert("Vehicle not found. Please check the license plate or vehicle name.")
    }
  }

  const getStatusColor = (status: string) => {
    switch (status) {
      case "active": return "default"
      case "parked": return "secondary"
      case "maintenance": return "outline"
      case "offline": return "destructive"
      default: return "default"
    }
  }

  const getStatusIcon = (status: string) => {
    switch (status) {
      case "active": return "🚗"
      case "parked": return "🅿️"
      case "maintenance": return "🔧"
      case "offline": return "📵"
      default: return "🚗"
    }
  }

  const activeVehicles = trackingData.filter(v => v.status === "active").length
  const parkedVehicles = trackingData.filter(v => v.status === "parked").length
  const maintenanceVehicles = trackingData.filter(v => v.status === "maintenance").length
  const offlineVehicles = trackingData.filter(v => v.status === "offline").length

  return (
    <div className="container mx-auto px-4 py-8">
      <div className="mb-8">
        <h1 className="text-4xl font-bold mb-4">Vehicle Tracking</h1>
        <p className="text-xl text-gray-600">
          Real-time monitoring of your fleet vehicles
        </p>
      </div>

      {/* Stats Cards */}
      <div className="grid grid-cols-2 md:grid-cols-4 gap-4 mb-8">
        <Card>
          <CardHeader className="pb-2">
            <CardTitle className="text-sm font-medium text-gray-600">Active</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold text-green-600">{activeVehicles}</div>
          </CardContent>
        </Card>
        <Card>
          <CardHeader className="pb-2">
            <CardTitle className="text-sm font-medium text-gray-600">Parked</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold text-blue-600">{parkedVehicles}</div>
          </CardContent>
        </Card>
        <Card>
          <CardHeader className="pb-2">
            <CardTitle className="text-sm font-medium text-gray-600">Maintenance</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold text-yellow-600">{maintenanceVehicles}</div>
          </CardContent>
        </Card>
        <Card>
          <CardHeader className="pb-2">
            <CardTitle className="text-sm font-medium text-gray-600">Offline</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold text-red-600">{offlineVehicles}</div>
          </CardContent>
        </Card>
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
        {/* Vehicle List Sidebar */}
        <div className="lg:col-span-1">
          <Card>
            <CardHeader>
              <CardTitle>Vehicle List</CardTitle>
              <CardDescription>Track and monitor your fleet</CardDescription>
            </CardHeader>
            <CardContent className="space-y-4">
              {/* Quick Track Input */}
              <div className="space-y-2">
                <Label htmlFor="trackInput">Quick Track</Label>
                <div className="flex gap-2">
                  <Input
                    id="trackInput"
                    placeholder="Enter license plate..."
                    value={trackingInput}
                    onChange={(e) => setTrackingInput(e.target.value)}
                    onKeyPress={(e) => e.key === 'Enter' && handleTrackVehicle()}
                  />
                  <Button onClick={handleTrackVehicle} size="sm">
                    Track
                  </Button>
                </div>
              </div>

              <Separator />

              {/* Search and Filter */}
              <div className="space-y-2">
                <Input
                  placeholder="Search vehicles..."
                  value={searchTerm}
                  onChange={(e) => setSearchTerm(e.target.value)}
                />
                <Select value={statusFilter} onValueChange={setStatusFilter}>
                  <SelectTrigger>
                    <SelectValue placeholder="Filter by status" />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="all">All Status</SelectItem>
                    <SelectItem value="active">Active</SelectItem>
                    <SelectItem value="parked">Parked</SelectItem>
                    <SelectItem value="maintenance">Maintenance</SelectItem>
                    <SelectItem value="offline">Offline</SelectItem>
                  </SelectContent>
                </Select>
              </div>

              <Separator />

              {/* Vehicle List */}
              <div className="space-y-2 max-h-96 overflow-y-auto">
                {filteredVehicles.map((vehicle) => (
                  <div
                    key={vehicle.id}
                    className={`p-3 border rounded-lg cursor-pointer transition-colors hover:bg-gray-50 ${
                      selectedVehicle?.id === vehicle.id ? "border-blue-500 bg-blue-50" : ""
                    }`}
                    onClick={() => setSelectedVehicle(vehicle)}
                  >
                    <div className="flex items-center justify-between mb-2">
                      <div className="font-medium text-sm">{vehicle.licensePlate}</div>
                      <Badge variant={getStatusColor(vehicle.status) as any} className="text-xs">
                        {getStatusIcon(vehicle.status)} {vehicle.status}
                      </Badge>
                    </div>
                    <div className="text-xs text-gray-600 mb-1">{vehicle.vehicle}</div>
                    <div className="text-xs text-gray-500">
                      {vehicle.driver} • {vehicle.lastUpdate}
                    </div>
                    {vehicle.status === "active" && (
                      <div className="text-xs text-blue-600 mt-1">
                        {Math.round(vehicle.speed)} mph
                      </div>
                    )}
                  </div>
                ))}
              </div>

              {filteredVehicles.length === 0 && (
                <div className="text-center py-4 text-gray-500 text-sm">
                  No vehicles found
                </div>
              )}
            </CardContent>
          </Card>
        </div>

        {/* Map and Details */}
        <div className="lg:col-span-2 space-y-6">
          {/* Map Placeholder */}
          <Card>
            <CardHeader>
              <CardTitle>Live Map View</CardTitle>
              <CardDescription>
                {selectedVehicle 
                  ? `Tracking ${selectedVehicle.licensePlate} - ${selectedVehicle.vehicle}`
                  : "Select a vehicle to view its location"
                }
              </CardDescription>
            </CardHeader>
            <CardContent>
              <div className="bg-gray-100 rounded-lg h-64 md:h-80 flex items-center justify-center relative overflow-hidden">
                {/* Simulated Map Background */}
                <div className="absolute inset-0 bg-gradient-to-br from-green-100 to-blue-100"></div>
                <div className="absolute top-4 left-4 right-4 bottom-4 border-2 border-dashed border-gray-300 rounded"></div>
                
                {selectedVehicle ? (
                  <div className="relative z-10 text-center">
                    <div className="text-4xl mb-2">{getStatusIcon(selectedVehicle.status)}</div>
                    <div className="font-semibold">{selectedVehicle.licensePlate}</div>
                    <div className="text-sm text-gray-600">{selectedVehicle.location}</div>
                    <div className="text-xs text-gray-500 mt-1">
                      Lat: {selectedVehicle.coordinates.lat.toFixed(4)}, 
                      Lng: {selectedVehicle.coordinates.lng.toFixed(4)}
                    </div>
                    {selectedVehicle.status === "active" && (
                      <div className="mt-2 text-sm">
                        <Badge variant="default">
                          {Math.round(selectedVehicle.speed)} mph
                        </Badge>
                      </div>
                    )}
                  </div>
                ) : (
                  <div className="relative z-10 text-center text-gray-500">
                    <div className="text-4xl mb-2">🗺️</div>
                    <div>Select a vehicle to view on map</div>
                  </div>
                )}
              </div>
            </CardContent>
          </Card>

          {/* Vehicle Details */}
          {selectedVehicle && (
            <Card>
              <CardHeader>
                <CardTitle>Vehicle Details</CardTitle>
                <CardDescription>
                  Detailed information for {selectedVehicle.licensePlate}
                </CardDescription>
              </CardHeader>
              <CardContent>
                <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                  <div className="space-y-4">
                    <div>
                      <Label className="text-sm font-medium text-gray-600">Vehicle</Label>
                      <div className="font-semibold">{selectedVehicle.vehicle}</div>
                    </div>
                    <div>
                      <Label className="text-sm font-medium text-gray-600">Driver</Label>
                      <div className="font-semibold">{selectedVehicle.driver}</div>
                    </div>
                    <div>
                      <Label className="text-sm font-medium text-gray-600">Status</Label>
                      <div>
                        <Badge variant={getStatusColor(selectedVehicle.status) as any}>
                          {getStatusIcon(selectedVehicle.status)} {selectedVehicle.status}
                        </Badge>
                      </div>
                    </div>
                    <div>
                      <Label className="text-sm font-medium text-gray-600">Current Location</Label>
                      <div className="font-semibold">{selectedVehicle.location}</div>
                    </div>
                  </div>
                  
                  <div className="space-y-4">
                    <div>
                      <Label className="text-sm font-medium text-gray-600">Current Speed</Label>
                      <div className="font-semibold">
                        {selectedVehicle.status === "active" 
                          ? `${Math.round(selectedVehicle.speed)} mph`
                          : "0 mph"
                        }
                      </div>
                    </div>
                    <div>
                      <Label className="text-sm font-medium text-gray-600">Fuel Level</Label>
                      <div className="font-semibold">{Math.round(selectedVehicle.fuelLevel)}%</div>
                      <div className="w-full bg-gray-200 rounded-full h-2 mt-1">
                        <div 
                          className={`h-2 rounded-full ${
                            selectedVehicle.fuelLevel > 50 ? 'bg-green-500' :
                            selectedVehicle.fuelLevel > 25 ? 'bg-yellow-500' : 'bg-red-500'
                          }`}
                          style={{ width: `${selectedVehicle.fuelLevel}%` }}
                        ></div>
                      </div>
                    </div>
                    <div>
                      <Label className="text-sm font-medium text-gray-600">Mileage</Label>
                      <div className="font-semibold">{selectedVehicle.mileage.toLocaleString()} miles</div>
                    </div>
                    <div>
                      <Label className="text-sm font-medium text-gray-600">Last Update</Label>
                      <div className="font-semibold">{selectedVehicle.lastUpdate}</div>
                    </div>
                  </div>
                </div>

                <Separator className="my-4" />

                <div>
                  <Label className="text-sm font-medium text-gray-600">Current Route</Label>
                  <div className="font-semibold">{selectedVehicle.route}</div>
                </div>
              </CardContent>
            </Card>
          )}
        </div>
      </div>
    </div>
  )
}
